import uniLoadMore from '@/components/UniLoadMore/UniLoadMore.vue';
import empty from "@/components/empty/empty";

export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					page:0,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					page:0,
					text: '待付款',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					page:0,
					text: '待发货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					page:0,
					text: '待收货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 4,
					page:0,
					text: '待评价',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 5,
					page:0,
					text: '已完成',
					loadingType: 'more',
					orderList: []
				}
			],
		};
	},
	
	onLoad(options){
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		// +号是让可以隐式转换的字符串变为数字
		this.tabCurrentIndex = +options.state;




		if(options.state == 0){
			this.loadData()
		}

		
	},
	onShow(){
		// 每次页面显示都让它去请求第一页数据
		// 比如从订单详情页面跳转回来
		this.loadData('tabChange');
	},
	methods: {
		//获取订单列表
		loadData(source){
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			
			if(source === 'tabChange' && navItem.loaded === true){
				// 设置请求状态
				navItem.loadingType = 'more'
				// 先把原来的数据清除
				this.$set(this.navList[this.tabCurrentIndex],"orderList",[])
				// tab切换时每次都是请求第一页数据
				this.$set(this.navList[this.tabCurrentIndex],"page",0)
				// return;
			}
			if(navItem.loadingType === 'loading'){
				// 防止在加载中,重复去请求
				return;
			}
			if(navItem.loadingType === 'noMore'){
				// 如果没有数据不给予请求
				return;
			}

			navItem.loadingType = 'loading';
			this.$request.GET({
				url:this.$api.apiUrl.GET_ORDER_GET_LIST,
				data:{
					page:this.navList[this.tabCurrentIndex].page++,
					status:this.tabCurrentIndex
				}
			}).then(res=>{
				let list = this.navList[this.tabCurrentIndex];
				//添加不同状态下订单的表现形式
				let orderList = res.map(item=>{
					return item = Object.assign(item, this.orderStateExp(item.status,item.statusText));
				});
				this.$set(list,"orderList",list.orderList.concat(orderList))
				
				//判断是否还有数据， 有改为 more， 没有改为noMore 
				if(res.length>0){
					navItem.loadingType = 'more';
				}else{
					navItem.loadingType = 'noMore';
				}
				//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页组件
				this.$set(navItem, 'loaded', true);
			})
		}, 

		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
		},
		//删除订单
		deleteOrder(index){
			uni.showLoading({
				title: '请稍候'
			})
			setTimeout(()=>{
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		//取消订单
		cancelOrder(item){
			uni.showLoading({
				title: '请稍候'
			})
			setTimeout(()=>{
				let {stateTip, stateTipColor} = this.orderStateExp(9);
				item = Object.assign(item, {
					state: 9,
					stateTip, 
					stateTipColor
				})
				
				//取消订单后删除待付款中该项
				let list = this.navList[1].orderList;
				let index = list.findIndex(val=>val.id === item.id);
				index !== -1 && list.splice(index, 1);
				
				uni.hideLoading();
			}, 600)
		},

		//订单状态文字和颜色
		orderStateExp(state,statusText){
			let stateTip = '',
				stateTipColor = '#fa436a',
				btnArr = [];
			switch(+state){
				case 0:
					btnArr = ["订单详情"]
					stateTip = statusText; 
					stateTipColor = 'red';break;
				case 1:
					btnArr = ["订单详情","去支付"]
					stateTip = statusText;
					stateTipColor = 'red';break;
				case 2:
					btnArr = ["订单详情"]
					stateTip = statusText;
					stateTipColor = 'red';break;
				case 3:
					btnArr = ["查看物流","确认收货","订单详情"]
					stateTip = statusText; 
					stateTipColor = 'red';break;
				case 4:
					btnArr = ["去评价","订单详情"]
					stateTip = statusText; 
					stateTipColor = 'red';break;
				case 5:
					stateTip = statusText; 
					stateTipColor = 'red';break;	
					break;
			}
			return {stateTip, stateTipColor,btnArr};
		},
		// 查找物流
		searchLogistics(orderId){
			this.$request.POST({
				url:this.$api.apiUrl.POST_ORDER_LOGISTICS,
				data:{
					order_id:orderId
				}
			}).then(res=>{
				const result = res.errMsg;
				uni.showModal({
					content: result.shipping_code+" : "+result.shipping_name,
					showCancel: false,
					confirmText: "确定"
				})
			})
		},
		// 确认收货
		confirmReceipt(orderId){
			uni.showModal({
				title: '消息提示',
				content: '确定已收到货了吗?',
				success:(res)=> {
					if (res.confirm) {
						this.$request.POST({
							url:this.$api.apiUrl.POST_ORDER_RECEIPT,
							data:{
								id:orderId
							}
						}).then(res=>{
							// console.log(res);
						})
					} else if (res.cancel) {return false}
				}
			});
		},
		operationOrder(e){
			let txt = e.target.dataset.txt,
				id 	= e.target.dataset.id;
			switch (txt) {
				case "订单详情":
					uni.navigateTo({
						url:`/pages/mys/orderInfo/orderInfo?id=${id}`
					})
					break;
				case "去支付":
					uni.navigateTo({
						url:`/pages/mys/orderInfo/orderInfo?id=${id}`
					})
					break;
				case "查看物流":
					this.searchLogistics(id);
				case "确认收货":
					this.confirmReceipt(id);
				default:
					break;
			} 
			
		}
	},
}
